import { EntityHandlersNames } from "../../constants/entity-handlers/EntityHandlersNames";
import { AnnotationMessages } from "../../constants/messages/annotation-messages";
import { IEntityHandler } from "../../interfaces/entity-handler/IEntityHandler";
import { inferParamType } from "../utils/code/js/types";
import { textProcessorTools } from "../utils/text/TextProcessorTools";

const {
  eliminateKeywords,
  isMultiline,
  lines,
  buildParamAnnot,
  removeArrayData,
  hasMoreThenOneParam,
  removeObjectData,
  hasNoWords,
  concatAnnotationAndSignature,
} = textProcessorTools;

export class FunctionHandler implements IEntityHandler {
  id = EntityHandlersNames.Function;
  text = "";

  constructor(text: string) {
    this.text = text;

    this.handle = this.handle.bind(this);
  }

  private deleteEntityFeatures(func: string): string {
    return func.replace(/[=(){}]/gim, "");
  }

  private deletePunctuation(str: string): string {
    return str.replace(/[.\n,]/gim, "");
  }

  private formatParamData(
    paramName: string,
    paramFallback: string
  ): [string, string] {
    const param = this.deleteEntityFeatures(this.deletePunctuation(paramName));
    const type = paramFallback
      ? inferParamType(removeObjectData(this.deletePunctuation(paramFallback)))
      : "";
    return [param, type];
  }

  private handleSingleParam(indent: string, funcarg: string): string {
    const [paramName, paramFallback] = funcarg.split("=");
    if (!paramName) return "";
    const [param, type] = this.formatParamData(paramName, paramFallback);
    return buildParamAnnot(indent, param, type);
  }

  handle(): string {
    const funcSignature = eliminateKeywords(
      this.text.replace(/^.*?=.*?(?=\()/gi, "").replace(/ */gim, "")
    );
    const indent = this.text.match(/^ +/gi)?.[0] || "";
    let annotation = `${indent}/**\n${indent} * ${AnnotationMessages.Function}\n`;
    if (isMultiline(funcSignature)) {
      lines(funcSignature).forEach((line, index) => {
        if (index === 0 && !hasMoreThenOneParam(line)) return;
        if (hasMoreThenOneParam(line)) {
          removeArrayData(line)
            .split(",")
            .forEach((funcarg) => {
              annotation += this.handleSingleParam(indent, funcarg);
            });
        } else if (!hasNoWords(line)) {
          annotation += this.handleSingleParam(indent, line);
        }
      });
      return concatAnnotationAndSignature(
        `${annotation}${indent} */`,
        this.text
      );
    }
    let [, line] = funcSignature.split(/\w+\(/gim);
    if (!line) line = funcSignature;
    if (hasNoWords(line)) {
      return `${indent}/** ${AnnotationMessages.Function} */\n${this.text}`;
    }
    removeArrayData(line)
      .split(",")
      .forEach((funcarg) => {
        annotation += this.handleSingleParam(indent, funcarg);
      });
    return concatAnnotationAndSignature(`${annotation}${indent} */`, this.text);
  }
}
