import { CopyIcon, DeleteIcon } from "@chakra-ui/icons";
import {
  Box,
  Button,
  ButtonGroup,
  ChakraProvider,
  Code,
  HStack,
  Heading,
  Radio,
  RadioGroup,
  Stack,
  Textarea,
  useToast,
} from "@chakra-ui/react";
import React, { useEffect, useState } from "react";
import Highlight from "react-highlight";
import { IntlProvider, useIntl } from "react-intl";
import { JSCodeProcessor } from "./classes/text-processor/JSCodeProcessor";
import { locales } from "./resources/locales";

const stackSx = {
  pre: {
    height: "100% !important",
    code: {
      width: "100%",
    },
  },
};

const processor = new JSCodeProcessor();

function App() {
  const [locale, setLocale] = useState<"en" | "ru">("en");

  return (
    <IntlProvider messages={locales[locale]} locale="en" defaultLocale="en">
      <View setLocale={setLocale} locale={locale} />
    </IntlProvider>
  );
}

export type ViewProps = {
  setLocale: (locale: "en" | "ru") => void;
  locale: "en" | "ru";
};

export const View = (props: ViewProps): JSX.Element => {
  const { setLocale, locale } = props;

  const toast = useToast();
  const { formatMessage } = useIntl();
  const codeOutputWindowDefaultMessage = formatMessage({
    id: "pressGenerateAnnotations",
  });

  const [userInput, setUserInput] = useState<string>("");
  const [annotation, setAnnotation] = useState<string>(
    codeOutputWindowDefaultMessage
  );

  useEffect(() => {
    setAnnotation((prev) => {
      if (prev === codeOutputWindowDefaultMessage) {
        return formatMessage({
          id: "pressGenerateAnnotations",
        });
      }
      return prev;
    });
  }, [locale]);

  const handleUserInput = (e: React.FormEvent<HTMLTextAreaElement>): void => {
    setUserInput(e.currentTarget.value);
  };

  const annotate = (): void => {
    try {
      setAnnotation(processor.process(userInput));
    } catch (e: any) {
      console.error(e);
      //   toast({ //idk about that
      //     title: e.message,
      //     status: "error",
      //   });
      setAnnotation(userInput || codeOutputWindowDefaultMessage);
    }
  };

  const clearUserCode = (): void => {
    setUserInput("");
  };

  const clearAnnotation = (): void => {
    setAnnotation(codeOutputWindowDefaultMessage);
  };

  const copy = (msg: string): void => {
    try {
      navigator.clipboard.writeText(msg);
      toast({
        status: "success",
        title: formatMessage({ id: "codeCopied" }),
      });
    } catch {
      toast({
        status: "error",
        title: formatMessage({ id: "codeNotCopied" }),
      });
    }
  };

  const copyUserCode = (): void => copy(userInput);

  const copyAnnotatedCode = (): void => copy(annotation);

  const toggleLocale = (value: "en" | "ru"): void => {
    setLocale(value);
  };

  return (
    <ChakraProvider>
      <HStack px={10} pt={10}>
        <Heading flex={1}>Annotate.js</Heading>
        <RadioGroup onChange={toggleLocale} value={locale}>
          <Stack direction="row">
            <Radio value="en">English</Radio>
            <Radio value="ru">Not English</Radio>
          </Stack>
        </RadioGroup>
      </HStack>
      <HStack p={10} h="78vh" sx={stackSx}>
        <Box w="50%" h="100%" position="relative" p={0}>
          <Toolbar
            onCopyButtonClick={copyUserCode}
            onTrashcanClick={clearUserCode}
          />
          <Code
            h="100%"
            p={5}
            onChange={handleUserInput}
            value={userInput}
            overflow="auto"
            as={Textarea}
            placeholder={formatMessage({ id: "yourCodeIsHere" })}
            resize="none"
          />
        </Box>
        <Box w="50%" h="100%" position="relative" p={0}>
          <Toolbar
            onCopyButtonClick={copyAnnotatedCode}
            onTrashcanClick={clearAnnotation}
          />
          <Code
            h="100%"
            p={5}
            overflowY="auto"
            overflowX="hidden"
            as={Highlight}
          >
            {annotation}
          </Code>
        </Box>
      </HStack>
      <ButtonGroup px={10}>
        <Button onClick={annotate}>
          {formatMessage({ id: "generateAnnotations" })}
        </Button>
      </ButtonGroup>
    </ChakraProvider>
  );
};

export type ToolbarProps = {
  onTrashcanClick: VoidFunction;
  onCopyButtonClick: VoidFunction;
};

export const Toolbar = (props: ToolbarProps): JSX.Element => {
  const { onTrashcanClick, onCopyButtonClick } = props;
  const [forcedlyVisibile, setForcedlyVisible] = useState<boolean>(true);

  useEffect(() => {
    setTimeout(() => setForcedlyVisible(false), 1500);
  }, []);

  return (
    <HStack
      position="absolute"
      right={0}
      zIndex={5}
      p={5}
      spacing="15px"
      bg="gray.200"
      opacity={forcedlyVisibile ? 1 : 0}
      transition="all .5s"
      _hover={{
        opacity: 1,
      }}
    >
      <CopyIcon cursor="pointer" onClick={onCopyButtonClick} />
      <DeleteIcon cursor="pointer" onClick={onTrashcanClick} />
    </HStack>
  );
};

export default App;
