export const inferParamType = (param: string): string => {
  if (param === "") {
    return "string";
  }
  if (param.match(/\b(null|undefined|NaN)\b/)) {
    return "";
  }
  if (param.match(/\b(true|false)\b/)) {
    return "boolean";
  }
  if (param.match(/\(.*\)/gim)) {
    return "Function";
  }
  if (param.match(/\{.*\}/gim)) {
    return "Object";
  }
  if (param.match(/\[.*\]/gim)) {
    return "Array";
  }
  return Number(param.replace(/[^\w]/g, "")) ? "number" : typeof param;
};
