class TextProcessorTools {
  eliminateKeywords(str: string): string {
    let filtered = str;
    [
      "const",
      "let",
      "var",
      "export",
      "async",
      "React.",
      "react",
      "useCallback(",
      "memo(",
      "observer(",
      "forwardRef(",
      "function",
    ].forEach((item) => {
      filtered = filtered.replace(item, "").trim();
    });
    return filtered;
  }

  buildParamAnnot(indentation: string, param: string, type?: string): string {
    if (type) return `${indentation} * @param {${type}} ${param} -\n`;
    return `${indentation} * @param ${param} -\n`;
  }

  isMultiline(text: string): boolean {
    return text.split("\n").length > 1;
  }

  hasMoreThenOneParam(text: string): boolean {
    /**  ,        -    = 1 */
    return text.split(",").length > 1;
  }

  hasNoWords(text: string): boolean {
    return !text || !text.match(/\w+/gi);
  }

  lines(text: string): string[] {
    return text.split("\n");
  }

  mergeRepeatingSymbols(str: string, symbls: string[]): string {
    let newStr = str;
    symbls.forEach(
      (symbl) =>
        (newStr = newStr.replace(new RegExp(`\\${symbl}+`, "gi"), symbl))
    );
    return newStr;
  }

  removeArrayData(arrayStr: string): string {
    return arrayStr.replace(/\[.*?\]/, "[]");
  }

  removeObjectData(arrayStr: string): string {
    return arrayStr.replace(/\{.*?\}/, "{}");
  }

  concatAnnotationAndSignature(annotation: string, signature: string): string {
    return `${annotation}\n${signature}`;
  }
}

export const textProcessorTools = new TextProcessorTools();
