import { EntityHandlersNames } from "../../constants/entity-handlers/EntityHandlersNames";
import { IEntityDetector } from "../../interfaces/detectors/IEntityDetector";
import { IEntityHandler } from "../../interfaces/entity-handler/IEntityHandler";
import { LambdaHandler } from "../entity-handlers/LambdaHandler";

const regexes = [
  /( *const|export const| *let|export let) \w+? = (React\.)?forwardRef\(\(.*?, \w+\) => {/gim,
  /( *const|export const| *let|export let) \w+? = (React\.)?(useCallback|forwardRef|observer|memo)\(\s*(async )?\([^%*@]*?\) =>\s{?(?!.*},)/gim,
  /( *const|export const| *let|export let) \w+? = (async )?\([^%*@]*?\) =>\s{?(?!.*},)/gim,
  /( *const|export const| *let|export let) \w+? = (async )?\w+ =>\s{?(?!.*},)/gi,
  /( *const|export const| *let|export let) \w+? = \([^%*@]*?\) =>\s{?(?!.*},)/gi,
];

export class LambdaDetector implements IEntityDetector {
  id = EntityHandlersNames.Lambda;

  detect(text: string): IEntityHandler[] {
    const matches: Set<string> = new Set();
    let possibleMatch: RegExpExecArray | null;
    let skip = false;
    for (const regex of regexes) {
      while ((possibleMatch = regex.exec(text))) {
        const [candidate] = possibleMatch;
        const matchesArray = [...matches];
        if (matchesArray.some((match) => match.includes(candidate))) {
          continue;
        }
        matchesArray.forEach((match, i) => {
          if (candidate.includes(match) && candidate.length > match.length) {
            skip = true;
            matchesArray[i] = candidate;
            matches.clear();
            matchesArray.forEach((match) => matches.add(match));
          }
        });
        if (skip) {
          skip = false;
        } else {
          matches.add(candidate);
        }
      }
    }
    return [...matches].map((match) => new LambdaHandler(match));
  }
}
